<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: 100%"></QueryBox>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"></Table>
        <div class="page_box">
          <Page :total="total" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- 查看-->
    <div class="extract_box" :class="{addActive:addActive}">
      <activitiesInspectTableMap ref="activitiesInspectTableMap" @signOut="signOut"></activitiesInspectTableMap>
    </div>
    <!-- 提取任务中的人类活动列表 -->
    <div class="detail-table-box" :class="{detailActive: detailActive}">
      <div class="queryBox" style="text-align: right">
        <Button type="success" size="small" icon="md-add" class="btn-add" @click="openAdd">添加至台账</Button>
        <Button type="primary" size="small" @click="detailActive = false" class="ml10">返回</Button>
      </div>
      <div class="detail-table">
        <Table :data="switchShowData"  border stripe
         :loading="loading2"  highlight-row :columns="columns2"
         @on-select-all="selectDetailAll"
         @on-select-all-cancel="unSelectDetailAll"
         @on-select="selectDetail"
         @on-select-cancel="unSelectDetail"></Table>
        <Page :total="total2" :current.sync="pageNum2" :page-size="pageSize2"/>
      </div>
    </div>
    <!-- 新建-->
    <Modal
      title="添加至台账"
      v-model="modal">
      <Form :label-width="120">
        <FormItem label="是否新建台账">
          <RadioGroup v-model="isCreateAccount">
            <Radio :label="1">是</Radio>
            <Radio :label="0">否</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem v-show="!isCreateAccount" id="notCreateAccount">
          <Table :data="data3" :columns="columns3" border highlight-row @on-current-change="changeAccount" :max-height="250" style="margin-left: -120px;"></Table>
          <Page :total="total3" :page-size="pageSize3" :current.sync="pageNum3" @on-change="pageChange" show-total style="width: 100%;padding: 15px 0;text-align: right;"/>
        </FormItem>
      </Form>
      <Form ref="addForm" :model="AddData" :rules="ruleValidate" :label-width="120" v-show="isCreateAccount">
        <FormItem prop="name" label="台账名称：">
          <Input v-model.trim="AddData.name" placeholder="请输入任务名称" size="default"/>
        </FormItem>
        <FormItem prop="remark" label="说明：">
          <Input v-model.trim="AddData.remark" placeholder="请输入任务名称" size="default" type="textarea"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="clickAdd">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import activitiesInspectTableMap from './activitiesInspectTable_map'
import { getRemoteList, getDetailList, getAccountList, createAccount1, addDataToGroup } from '@/api/remoteSensing'
export default {
  name: 'extracTable',
  data () {
    return {
      /* 查询框查询条件 */
      queryData: {},
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '说明',
          align: 'center',
          minWidth: 250,
          key: 'remark'
        },
        {
          title: '底图服务',
          align: 'center',
          width: 300,
          key: 'url'
        },
        {
          title: '操作',
          key: 'address',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    // this.addActive = true
                    // this.$refs.activitiesInspectTableMap.initCom()
                    this.detailActive = true
                    this.getDetailList(params.row.id)
                  }
                }
              }, '查看人类活动点')
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false,
      /* 提取信息框状态 */
      addActive: false,
      modalStatus: '', // 模态框状态
      /* 添加的弹框-表单参数 */
      AddData: {
        name: '',
        remark: ''
      },
      /* 添加的弹框-表单校验 */
      ruleValidate: {
        name: [
          { required: true, message: '台账名称必须填写', trigger: 'blur' }
        ],
        remark: [
          { required: true, message: '台账说明必须填写', trigger: 'blur' }
        ]
      },
      /* 添加的弹框-类型参数 */
      addTypeList: [
        {
          value: '1',
          label: 'type1'
        },
        {
          value: '2',
          label: 'type2'
        }
      ],
      /* 人类活动详情列表的面板 */
      detailActive: false,
      /* 人类活动详情列表数据 */
      data2: [],
      /* 人类活动详情列表的列配置 */
      columns2: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '活动名称',
          align: 'center',
          minWidth: 200,
          key: 'activeName'
        },
        {
          title: '活动类型',
          align: 'center',
          minWidth: 200,
          key: 'activeType'
        },
        {
          title: '所属区域类型',
          align: 'center',
          minWidth: 200,
          key: 'region'
        },
        {
          title: '位置',
          align: 'center',
          minWidth: 200,
          key: 'position'
        },
        {
          title: '面积（公顷）',
          align: 'center',
          minWidth: 200,
          key: 'area'
        }
      ],
      /* 人类活动详情列表的分页器 */
      total2: 0,
      pageNum2: 1,
      pageSize2: 10,
      /* 人类活动表格的加载中动画 */
      loading2: false,
      /* 是否新建台账 */
      isCreateAccount: 1,
      /* 台账页面的分页器当前页数 */
      pageNum3: 1,
      pageSize3: 10,
      /* 台账页面的分页器的总条数 */
      total3: 0,
      /* 台账页面的表格数据 */
      data3: [],
      /* 台账页面的列设置 */
      columns3: [
        {
          title: '序号',
          key: 'index',
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum3 - 1) * 10 + 1)
          }
        },
        {
          title: '台账名称',
          key: 'name',
          align: 'center'
        }
      ],
      /* 当前选中台账数据 */
      currentRow: null,
      /* 添加至弹框的控制变量 */
      modal: false
    }
  },
  components: {
    QueryBox,
    // 人类活动核查信息地图
    activitiesInspectTableMap
  },
  computed: {
    // 查询表单
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'name',
          title: '任务名称'
        }
      ]
    },
    // 详情表格展示的数据
    switchShowData () {
      if (this.data2.length) {
        console.log(this.data2)
        return this.data2.slice((this.pageNum2 - 1) * this.pageSize2, this.pageNum2 * this.pageSize2)
      } else {
        return []
      }
    }
  },
  created () {
    this.getTableList()
    this.getAccountList()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  watch: {
    queryData () {
      this.getTableList()
    }
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getRemoteList({
        'data': {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total1 = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* 信息提取地图页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    },
    /* 获取人类活动详情列表 */
    getDetailList (id) {
      this.loading2 = true
      getDetailList({
        data: {
          pageNum: this.pageNum2,
          pageSize: this.pageSize2,
          id
        }
      }).then(res => {
        this.loading2 = false
        if (res.data.code === '0000') {
          this.data2 = res.data.data.data
          this.total2 = this.data2.total
          // this.pageNum2 = 1
        } else {
          this.data2 = []
          this.$Notice.error({ title: '获取人类活动详情列表失败', desc: res.data.msg })
        }
      })
    },
    /* 获取台账列表 */
    getAccountList () {
      getAccountList({
        data: {
          pageSize: this.pageSize3,
          pageNum: this.pageNum3
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.data3 = res.data.data.rows
          this.total3 = res.data.data.total
        } else {
          this.$Notice.error({ title: '获取人类活动台账失败', desc: res.data.msg })
        }
      })
    },
    /* 新建台账并添加人类活动点 */
    createAndAdd (idArr) {
      /* 请求创建添加数据的接口 */
      createAccount1({
        data: {
          name: this.AddData.name,
          id: idArr,
          remark: this.AddData.remark,
          // todo 用户id
          createBy: 1
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '成功添加至新台账' })
        } else {
          this.$Notice.error({ title: '添加至新台账失败', desc: res.data.msg })
        }
      })
    },
    /* 选中人类活动行的函数 */
    selectDetail (selection, row) {
      this.data2.some(value => {
        if (row.cd001 === value.cd001) {
          value._checked = true
          return true
        } else {
          return false
        }
      })
    },
    /* 取消选中人类活动行的函数 */
    unSelectDetail (selection, row) {
      this.data2.some(value => {
        if (row.id === value.id) {
          value._checked = false
          return true
        } else {
          return false
        }
      })
    },
    /* 全部选中的函数 */
    selectDetailAll () {
      this.data2.slice((this.pageNum2 - 1) * this.pageSize2, this.pageNum2 * this.pageSize2).forEach(val => {
        val._checked = true
      })
    },
    /* 全部取消选中的函数 */
    unSelectDetailAll () {
      this.data2.slice((this.pageNum2 - 1) * this.pageSize2, this.pageNum2 * this.pageSize2).forEach(val => {
        val._checked = false
      })
    },
    /* 添加数据到已有台账 */
    addToGroup (id, ids) {
      addDataToGroup({
        data: {
          groupId: id,
          id: ids
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '添加到台账成功' })
        } else {
          this.$Notice.error({ title: '添加人类活动至台账失败', desc: res.data.msg })
        }
      })
    },
    /* 点击添加至台账的按钮函数 */
    clickAdd () {
      let idArr = this.data2.filter(val => {
        return val._checked
      }).map(val => {
        return val.cd001
      })
      if (this.isCreateAccount) {
        this.createAndAdd(idArr)
      } else {
        this.addToGroup(this.currentRow.id, idArr)
      }
    },
    /* 选中台账的函数 */
    changeAccount (currentRow) {
      this.currentRow = currentRow
    },
    /* 打开添加至台账的弹框 */
    openAdd () {
      this.modal = true
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      line-height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .extract_box,.detail-table-box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .detail-table{
      padding: 0 10px;
      height: calc(100% - 80px);
      .ivu-page {
        width: 100%;
        padding: 15px;
        text-align: right;
      }
    }
    .operator-btn{
      padding: 10px;
    }
    .addActive{
      right:0;
    }
    .detailActive {
      right: 0
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .ml10 {
      margin-left: 10px;
    }

  }
</style>
